import BigNumber from'bignumber.js';export var buildLocalTxStatusSyncId=function(details){return`${details.provider.name.toLowerCase()}-${details.token.info.symbol.toLowerCase()}`;};export function capitalizeString(str){if(!str)return str;return str.charAt(0).toUpperCase()+str.slice(1);}export function countDecimalPlaces(input){var inputNum=typeof input==='string'?Number(input):input;if(Number.isNaN(inputNum)){return 0;}var inputStr=typeof input==='string'?input:BigNumber(input).toFixed();var decimalIndex=inputStr.indexOf('.');if(decimalIndex===-1){return 0;}return inputStr.length-decimalIndex-1;}